import React from 'react';
import AddClient from './AddClient.js';
import CustomerSignUpForm from './CustomerSignUp.js';
import RegistrationForm from './Form.js';


class ContentViewer extends React.Component{
 
    render(){
        var url=window.location.href;
        const lastItem = url.substring(url.lastIndexOf('/') + 1);
        alert(lastItem);
        if(lastItem == '')           
          {
           var contentView =   <RegistrationForm /> ;
              
          }
          else if(lastItem == 'customer-sign'){
            var contentView =   <CustomerSignUpForm /> ;
          }
          else if(lastItem == 'register'){
            var contentView =   <RegistrationForm /> ;
          }
          else if(lastItem == 'client-sign'){
            var contentView =   <AddClient /> ;
          }
          
        return(

            <div>
          
           {contentView}
          </div>
          
        );

}
}

export default ContentViewer