import React from 'react';
import {Avatar, Layout, Menu} from 'antd';
import 'antd/dist/antd.css';
import '../App.css';

import {
  Bell,
  Maximize,
  Settings,
  Triangle
} from 'react-feather';

import {
  Row,
  Col,
} from 'antd';

import {UserOutlined} from '@ant-design/icons';

const { Header } = Layout;
const { SubMenu } = Menu;

class TopNav extends React.Component{

    render(){
        return(
          <Header className="ant-layout-header site-layout-sub-header-background" style={{ padding: 0,backgroundColor: 'white', position: 'fixed', zIndex: 1, width: '100%'  }}>
          <div className="container">
          <Row>
        <Col xs={5} sm={4} md={4} lg={4} xl={8}>
       <a className="brand" href=""> <Triangle size={27} strokeWidth={1} /></a>
        </Col>
        <Col xs={13} sm={12} md={10} lg={10} xl={10}>
        <span  className="headerText"><small>Webkart SuperAdmin</small></span>
        </Col>
        <Col  xs={6} sm={8} md={10} lg={10} xl={6}>
          <Menu mode="horizontal">
                <Menu.Item key="5"><Maximize size={20} strokeWidth={1} /></Menu.Item>
                  <Menu.Item key="6"><Settings size={20} strokeWidth={1} /></Menu.Item>
                  <Menu.Item key="7"><Bell size={20} strokeWidth={1} /></Menu.Item>
                  <SubMenu title={<Avatar size="small" className="ant-dropdown-link" strokeWidth={1} icon={<UserOutlined />} />}>
                <Menu.Item>Settings</Menu.Item>
                <Menu.Item>Profile</Menu.Item>
                <Menu.Item>Notifications</Menu.Item>
                <Menu.Divider />
                <Menu.Item>
                  
                    <a>Help?</a>
                  
                </Menu.Item>
                <Menu.Item>Signout</Menu.Item>
              </SubMenu>
            </Menu>
        </Col>
      </Row></div>
          </Header>
                        
    
      
        );
    }
}
export default TopNav