import React from 'react';
import { Layout, Divider } from 'antd';

import 'antd/dist/antd.css';
import {
  Form,
  Input,
  Select,
  Checkbox,
  Button,
} from 'antd';

const { Content } = Layout;
const { Option } = Select;

const formItemLayout = {
  labelCol: {
    md: {
      span: 6,
    },
    xs: {
      span: 24,
    },
    sm: {
      span: 8,
    },
  },
  wrapperCol: {
    md: {
      span: 16,
    },
    xs: {
      span: 24,
    },
    sm: {
      span: 16,
    },
  },
};
const tailFormItemLayout = {
  wrapperCol: {
   
    xs: {
      span: 24,
      offset: 0,
    },
    sm: {
      span: 16,
      offset: 8,
    },
  },
};

const RegistrationForm = () => {
    
    return (

<div>
 
<Content className="content">
        <div className="" style={{ padding: '0px', minHeight: 360 }}>
         
      <div className="form">
      <Divider orientation="left">
            <small>Add New Customer</small> 
      </Divider>
    <Form {...formItemLayout}  name="register" >  
     
     <Form.Item name="name" label="Name" style={{paddingTop:'20px'}}
          rules={[
            {
              required: true,
            },
          ]}
        >
          <Input placeholder="Customer Name"  />
     </Form.Item>

      <Form.Item name="organisationName" label="Organisation Name"
        rules={[
          {
            required: true,
          },
        ]}
         >
        <Input placeholder="Organisation Name"/>
      </Form.Item>
          
      <Form.Item name="phone" label="Contact No"
        rules={[
          {
            required: true,
           
          },
        ]}
        >
        <Input placeholder="Contact No" />
      </Form.Item>

      <Form.Item
        name="phone"
        label="Contact No2"
        rules={[
          {
            required: true,
           
          },
        ]}
      >
        <Input placeholder="Contact No 2" style={{width: '100%',}} />
      </Form.Item>

            
            
            <Form.Item
        name="email"
        label="E-mail"
        rules={[
          {
            required: true,
            type: 'email',
            message: 'The input is not valid E-mail!',
          },
          {
            required: true,
            
          },
        ]}
      >
        <Input placeholder="Email Address" />
      </Form.Item>

      <Form.Item
        name="address"
        label="Address"
        rules={[
          {
            required: true,
          },
        ]}
      >
                <Input placeholder="Address"  />
            </Form.Item><Form.Item
        name="city"
        label="City"
        rules={[
          {
            required: true,
          },
        ]}
      >
                <Input placeholder="City"  />
            </Form.Item>

            
            <Form.Item
        name="state"
        label="State"
        rules={[
          {
            required: true,
          },
        ]}
      >
                <Input placeholder="State"  />
            </Form.Item>
            <Form.Item
        name="country"
        label="Country"
        rules={[
          {
            required: true,
          },
        ]}
      >
                <Input placeholder="Country"  />
            </Form.Item>



            <Form.Item
        name="pinCode"
        label="Zip Code/Pin Code"
        rules={[
          {
            required: true,
          },
        ]}
      >
                <Input placeholder="Zip Code/Pin Code"  />
            </Form.Item>


            <Form.Item
            name="select-multiple"
            label="Package"
            rules={[
                {
                required: true,
                message: 'Select Packages',
                type: 'array',
                },
            ]}
            >
            <Select mode="multiple" placeholder="Package">
                <Option value="red">Red</Option>
                <Option value="green">Green</Option>
                <Option value="blue">Blue</Option>
            </Select>
            </Form.Item>
        
      
      <Form.Item
        name="agreement"
        valuePropName="checked"
        rules={[
          {
            validator: (_, value) =>
              value ? Promise.resolve() : Promise.reject('Should accept agreement'),
          },
        ]}
        {...tailFormItemLayout}
      >
        <Checkbox>
          I have read the <a href="">agreement</a>
        </Checkbox>
      </Form.Item>
      <Form.Item {...tailFormItemLayout}>
        <Button type="primary" htmlType="submit">
          Register
        </Button>
      </Form.Item>
    </Form></div>
    
        </div>
      </Content>
      
      </div>
    );
}

export default RegistrationForm