import React from 'react';
import {Layout, Divider } from 'antd';
import 'antd/dist/antd.css';
import {
  Form,
  Input,
  Checkbox,
  Button,
} from 'antd';
const { Content } = Layout;

const formItemLayout = {
  labelCol: {
    md: {
      span: 6,
    },
    xs: {
      span: 24,
    },
    sm: {
      span: 8,
    },
  },
  wrapperCol: {
    md: {
      span: 16,
    },
    xs: {
      span: 24,
    },
    sm: {
      span: 16,
    },
  },
};
const tailFormItemLayout = {
  wrapperCol: {
   
    xs: {
      span: 24,
      offset: 0,
    },
    sm: {
      span: 16,
      offset: 8,
    },
  },
};

const CustomerSignUpForm = () => {

    return(

        
<div>
 
 <Content className="content">
         <div className="" style={{ padding: '0px', minHeight: 360 }}>
          
       <div className="form">
       <Divider orientation="left">
             <small>Sign up</small> 
       </Divider>
     <Form {...formItemLayout}  name="register" >  
      
      <Form.Item name="name" label="Name" style={{paddingTop:'20px'}}
           rules={[
             {
               required: true,
             },
           ]}
         >
           <Input placeholder="Name"  />
      </Form.Item>
 

      <Form.Item
        name="email"
        label="E-mail"
        rules={[
          {
            required: true,
            type: 'email',
            message: 'The input is not valid E-mail!',
          },
          {
            required: true,
            
          },
        ]}
      >
        <Input placeholder="Email Address" />
      </Form.Item>

      <Form.Item
        label="Password"
        name="password"
        rules={[{ required: true, message: 'Please input your password!' }]}
      >
        <Input.Password placeholder="Password"/>
      </Form.Item>
           
       <Form.Item name="phone" label="Mobile No"
         rules={[
           {
             required: true,
            
           },
         ]}
         >
         <Input placeholder="Mobile No" />
       </Form.Item>


       
      <Form.Item
        name="agreement"
        valuePropName="checked"
        rules={[
          {
            validator: (_, value) =>
              value ? Promise.resolve() : Promise.reject('Should accept agreement'),
          },
        ]}
        {...tailFormItemLayout}
      >
        <Checkbox>
        I agree to your <a href="#">License Agreement </a> and  <a href="#">Privacy Policy</a>
          
        </Checkbox>
      </Form.Item>
      <Form.Item {...tailFormItemLayout}>
        <Button type="primary" htmlType="submit">
        Signup for FREE Trial
        </Button>
      </Form.Item>
    </Form>
    </div>
    
        </div>

        
      </Content>
      </div>
    );

}


export default CustomerSignUpForm 

